import React from 'react'
import { Link, graphql } from 'gatsby'
import Layout from '../components/layout' 

const CryptoList = ({ data }) => (
  <Layout>
    <h1>Data from .json file</h1>
    <ul>
      {data.allCryptoJson.edges.map(document => (
        <li key={document.node.code}>
          <p>{document.node.name}</p>
          <p>USD value: {document.node.usd}</p>
        </li>
      ))}
    </ul>
  </Layout>
)

export default CryptoList

export const query = graphql`
query CryptoList {
  allCryptoJson {
    edges {
      node {
    		name,
        code,
        usd
    	}
    }
  }
}
`